/* overpass-regular - latin */
@font-face {
  font-family: 'Overpass';
  font-style: normal;
  font-weight: 400;
  src: url('overpass/overpass-v12-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('overpass/overpass-v12-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('overpass/overpass-v12-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('overpass/overpass-v12-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('overpass/overpass-v12-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('overpass/overpass-v12-latin-regular.svg#Overpass') format('svg'); /* Legacy iOS */
}
